.header {
  max-width: 880px;
  margin: 0 auto;
  padding: 45px 0 40px 0;
  border-bottom: 1px solid rgba(84, 84, 84, 0.7);
}

@media screen and (max-width: 1023px) {
  .header {
    max-width: 730px;
  }
}

@media screen and (max-width: 767px) {
  .header {
    max-width: 500px;
  }
}

@media screen and (max-width: 568px) {
  .header {
    padding: 28px 0 31px 27px;
    max-width: 320px;
  }
}
